.contactList {
  display: flex;
  flex-wrap: wrap;
    width: 800px;
    gap: 30px;
}

.contactListItem {
  width: calc((100% - 60px) / 3);
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}


.contactListItemBtn {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  align-items: right;
  background-color: rgba(23, 26, 32, 0.8);
  border: none;
  border-radius: 6px;
  color: #fff;
  /* flex-grow: 1; */
  font-family: SFProText-Regular, Helvetica, Arial, sans-serif;
  font-size: 15px;
  height: 36px;
  line-height: 20px;
  margin-left: 8px;
  margin-right: 6px;
  padding: 0 16px 0 16px;
  color: white;
  cursor: pointer;
}

.contactListItemBtn:hover {
  background-color: rgba(23, 26, 32, 1);
  cursor: pointer;
}

